body {
  color: inherit;
}

.logo {
  display: flex;
  height: 32px;
  padding-left: 0;
  flex-wrap: nowrap;
  align-items: center;
}

.blur {
  position: absolute;
  left: 0;
  top: auto;
  right: 0;
  bottom: 0;
  overflow: hidden;
  height: 50%;
  border-bottom-left-radius: 8px;
  border-bottom-right-radius: 8px;
  background-color: #ff33;
  backdrop-filter: blur(4px);
}
